import Page from '../../../../components/page'
import Layout from '../../../../components/layout'
import Post from '../../../../components/post'
import {Container} from 'reactstrap';
import Link from 'next/link'
import React, { Component} from 'react';

export class Title extends React.Component {
  static text="Подбор технологий для разработки браузерных игр";
    render() { return (<h4>{Title.text}</h4>) }
}

export class PreviewContents extends React.Component {
    render() {
	return (<p>Исследование возможности создания полноценной игровой среды в рамках веб-ббраузера.</p>)
    }
}

export default class extends Post {
    fileid='1K10aJfvZaVIizslciZKLWE4KQez6nFEqrZPQmeE_u1k'
    preview() {
	return (<Container>
		<Title/>
		<PreviewContents/>
		<Link href={this.state.path}>Read more..</Link>
		</Container>)
    }
    post() {
	return (<Layout {...this.props} navmenu={false} title={Title.text}>
		<Title/>
		<PreviewContents/>

		<div dangerouslySetInnerHTML={{__html:require('./content.html')}}></div>

		</Layout>)
    }
}



