import Page from './page'
import { withRouter } from 'next/router'

export default class Post extends Page {
    state={};
    gists=[]; // github gists?
    fileid=null; // google doc id
    constructor(props,moduleid) {
	super(props);
	this.state.path = props.path;
    }
    
    render() {
	if (this.props.preview)
	    return this.preview()
	else
	    return this.post()
    }

}



